.homepage-about {
  margin-top: -150px;
  padding: 0 200px;
  font-family: 'Ubuntu';
  margin-bottom: - 80px;
}
.homepage-about>h2 {
  margin-bottom: 20px;
  font-family: 'Ubuntu';
  font-size: 50px;
}
.homepage-about>p{
  margin-top: 40px;
  font-size: 18px;
  line-height: 40px;
  font-weight: 400;
}
.button-container{
  margin: 0 auto;
  padding-bottom: 150px;
  height: 50px;
  width:200px;
  margin-top: 10px;
}
.more-button{
  height: 40px;
  width: 200px;
  border-radius: 4px;
  border-color: rgba(124,10,2,.5);
  background-color:rgba(255,255,255,.2);
  border-width: 1px;
  font-weight: 700;
  font-size: 20px;
  color: rgb(64,64,64);
  transition: .5s;

}
.more-button:hover{
  background-color:rgba(124,10,2,.5);
  transition: .5s;
  margin-top:-10px;
}
